export default {
	data() {
		return {
			buylist: [{
					type: "unpaid",
					ordersn: 0,
					goods_id: 0,
					img: '/static/img/goods/p1.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '168.00',
					payment: 168.00,
					freight: 12.00,
					spec: '规格:S码',
					numner: 1
				},
				{
					type: "unpaid",
					ordersn: 1,
					goods_id: 1,
					img: '/static/img/goods/p2.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '168.00',
					payment: 168.00,
					freight: 12.00,
					spec: '规格:S码',
					numner: 1
				},
				{
					type: "back",
					ordersn: 2,
					goods_id: 1,
					img: '/static/img/goods/p3.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '168.00',
					payment: 168.00,
					freight: 12.00,
					spec: '规格:S码',
					numner: 1
				},

			], //订单列表
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			freight: 12.00, //运费
			note: '', //备注
			int: 1200, //抵扣积分
			deduction: 0, //抵扣价格
			recinfo: {
				id: 1,
				name: "大黑哥",
				head: "大",
				tel: "18816881688",
				address: {
					region: {
						"label": "广东省-深圳市-福田区",
						"value": [18, 2, 1],
						"cityCode": "440304"
					},
					detailed: '深南大道1111号无名摩登大厦6楼A2'
				},
				isDefault: true
			}

		};
	},
	onShow() {
		//页面显示时，加载订单信息
		uni.getStorage({
			key: 'buylist',
			success: (ret) => {
				this.buylist = ret.data;
				this.goodsPrice = 0;
				//合计
				let len = this.buylist.length;
				for (let i = 0; i < len; i++) {
					this.goodsPrice = this.goodsPrice + (this.buylist[i].number * this.buylist[i].price);
				}
				this.deduction = this.int / 100;
				this.sumPrice = this.goodsPrice - this.deduction + this.freight;
			}
		});
		uni.getStorage({
			key: 'selectAddress',
			success: (e) => {
				this.recinfo = e.data;
				uni.removeStorage({
					key: 'selectAddress'
				})
			}
		})
	},
	onHide() {

	},
	onBackPress() {
		//页面后退时候，清除订单信息
		this.clearOrder();
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		clearOrder() {
			uni.removeStorage({
				key: 'buylist',
				success: (res) => {
					this.buylist = [];
					console.log('remove buylist success');
				}
			});
		},
		toPay() {
			//商品列表
			let paymentOrder = [];
			let goodsid = [];
			let len = this.buylist.length;
			for (let i = 0; i < len; i++) {
				paymentOrder.push(this.buylist[i]);
				goodsid.push(this.buylist[i].id);
			}
			if (paymentOrder.length == 0) {
				uni.showToast({
					title: '订单信息有误，请重新购买',
					icon: 'none'
				});
				return;
			}
			//本地模拟订单提交UI效果
			uni.showLoading({
				title: '正在提交订单...'
			})
			setTimeout(() => {
				uni.setStorage({
					key: 'paymentOrder',
					data: paymentOrder,
					success: () => {
						uni.hideLoading();
						uni.redirectTo({
							url: "../pay/payment/payment?amount=" + this.sumPrice
						})
					}
				})
			}, 1000)

		},
		//选择收货地址
		selectAddress() {
			uni.navigateTo({
				url: '../user/address/address?type=select'
			})
		}
	}
}
